












import { Component, Vue, Prop } from "vue-property-decorator";

@Component
export default class VsNewsSubstory extends Vue {
  @Prop({ required: true })
  private readonly news!: any;

  get headline() {
    return this.news.headline.length > 50
      ? this.news.headline.substr(0, 50) + "..."
      : this.news.headline;
  }
}
